@import '../css/partial/cookie.css';

:root {
	--primary: #073151;
	--secondary: #37a0ef;
	--secondary__light: rgba(55,160,239,0.5);
	--secondary__very__light:rgba(55,160,239,0.2);
	--complement: #000;
	--light-gray:rgba(132, 133, 134, 0.5);
	--dark: #363f44;
	--font-body: #54626a;
	--white: #fff;
	--blue: #007bff;
	--black: #000;
	--orange: #fd7e14
}

@font-face {
	font-family: proxima-nova;
	src: url("/fonts/proxima-nova.otf");
}

body {
	color: var(--font-body);
	height: 100%;
	margin: 0;
	padding: 0;
	font-family: "proxima-nova", sans-serif;
}

[data-scroll] {
	transition: all 0.5s;
}

[data-scroll="in"] {
	opacity: 1;
	transform: translateX(0);
}

[data-scroll="out"] {
	opacity: 0;
	transform: translateX(-200px);
}

[data-scroll-dir-y] {
	transition: 5s ease-in-out;
}

.clickable {
	cursor: hand;
}

/* STYLE -- cookie here */
.cc-color-override--1042357756.cc-window {
	color: rgb(255, 255, 255);
	background-color: red !important;
}
/* END cookie style */

a {
	text-decoration: none;
	color: var(--font-body);
}

a:active {
