#burger-top {
	background-color: var(--white);
	border-radius: 3px;
	width: 25px;
	min-height: 5px;
	margin: 10px 5px 5px 5px;
	transition: all 0.15s ease-in-out;
}

.burger-top-rotate {
	transform: rotate(45deg) translate(2px, 1px);
	transition: all 0.15s ease-in-out;
}

.burger-down-rotate {
	transform: rotate(-45deg) translate(6px, -6px);
	transition: all 0.15s ease-in-out;
}

#burger-mid {
	background-color: var(--white);
	border-radius: 3px;
	width: 25px;
	min-height: 5px;
	margin: 5px;
	transition: all 0.3s ease-in-out;
}

