.dropdown {
	display: none;
	font-size: 12px;
	position: absolute;
	border-radius: 2.5px;
	left: 0;
	margin-top: 20px;
	margin-left: -10px;
	right: 0;
	min-width: max-content;
	min-width: intrinsic;
  min-width: -moz-max-content;
  width: -webkit-max-content;
	max-width: 500px;
	background-color: var(--white);
	opacity: 0;
	text-align: left;
	visibility: hidden;
	color: var(--dark);
	transition: all 0.135s ease-in-out 0s;
	border: 1px solid rgba(216, 213, 213, 0.3);
