	display: block;
	padding: 10px 0;
}

/* apotica */
.dropdown::before {
	position: absolute;
	content: "";
	margin-top: -8.5px;
	height: 40px;
	width: 40px;
	background-color: var(--white);
	transform: rotate(45deg);
	text-align: left;
	margin-left: 17.5%;
	z-index: -1;
}

