	/* transform: scale(1.2); */
	animation: popup 0.3s ease-in-out 0s 1 normal forwards
}

@keyframes popup {
	0%{
		transform: scale(1);
	}

	50%{
		transform: scale(1.4);
	}

	75% {
		transform: scale(1.2);
	}

	100%{
		transform: scale(1);
	}
}

@keyframes shake {
	0% {
		transform: rotate(182deg);
	}

	25%{
		transform: rotate(185deg)
	}

	50% {
		transform: rotate(190deg)
	}

	75% {
		transform: rotate(185deg);
	}

	100% {
		transform: rotate(180deg);
	}
}
