}



.partner-logo img {
	width: 70%;
	height: 100%;
	align-items: center;
	text-align: center;
}

.partner-logo .reduce  {
	transform: scale(0.90);
}

.partner-logo .reduce-more {
	transform: scale(0.6);
}

.partner-logo .enlarge {
	transform: scale(1.5);
}

.partner-logo:hover + .partner-details {
	display: block;
}

.partner-logo:hover::after {
	visibility: visible;
}

.partner-details__head h1 {
	padding: 8px;
}

.partnership-interests__grid .ui.button {
	background: transparent;
	width: 100%;
}

.partnership-interests__grid .ui.popup{
	padding: 0;
}


.partnership-interests__grid ul {
	padding: 0;
	list-style-type: disc;
	margin-block-start: 0;
	margin-block-end: 0;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
	padding-inline-start: 0px;
}

.partner-details__content ul {
	padding: 10px 20px;
}

.partnership-interests__grid ul li{
	list-style: none;
	padding: 8px 0 0 0;
	margin: 0;
	/* float: left; */
	display: block;
	text-align: left;

}

.partnership-interests__grid ul li:first-child {
	padding: 0 0 0 0;
}

.partner-details__head {
	padding: 1px;
	background: #cfcece;
	font-weight: 900;
}

.partner-details__head h1 {
	padding: 8px;
	margin: 0;
}

/* START -- campaign style head */
.campaign-container {
	display: grid;
	grid-template-columns: 60% 40%;
	grid-gap: 30px;
	grid-template-rows: auto;
	grid-template-areas:
	"container-large container-small"
}

.campaign-container .campaign-content {
	width: 94%;
	margin-left: 3%;
	margin-right: 3%;
}

.campaign-container .campaign-content .content-row {
	display: grid;
	grid-template-columns: min-content auto;
	grid-column-gap: 20px;
	align-items: center;
}

.campaign-container .campaign-content .content-row_header h2 {
	margin-bottom: 20px;
	font-weight: 900;
	color: var(--black);
}

.campaign-container .campaign-content .content-row .img-holder {
	width: 140px;
}

.container-large {
	grid-area: container-large;
}

.container-large .campaign-content .content-caption {
	font-weight: 900;
	font-size: 18px;
}

.container-small {
	grid-area: container-small;
	background: #000;
	border-top: 4px solid var(--secondary);
}

.campaign-form-container {
	width: 90%;
	margin-left: 5%;
	background: #000;
}

.campaign-container .form-caption span,
.campaign-container .form-caption h2 {
	padding: 10px 0;
	color: var(--white);
}
.campaign-form-container form-body .input-wrap {
	width: 96%;
	margin-left: 2%;
	margin-right: 2%;
}

.campaign-form-container form-body input,
.campaign-form-container form-body textarea{
	width: calc(100% - 10px);
}

.campaign-form-container form-body input[type=submit] {
	width: 100%;
}

.campaign-form-container form-body .input-wrap input,
.campaign-form-container form-body .input-wrap textarea{
	border-radius: 2.5px;
	height: 50px;
	background: #000;
	color:#fff;
	border: 1px solid #fff;
	padding-left: 10px;
	font-size: 16px;
}

.campaign-form-container form-body .input-wrap input:focus,
.campaign-form-container form-body .input-wrap textarea:focus{
	outline: none;
	background: var(--light-gray);
}

.campaign-form-container form-body .input-wrap{
	margin-bottom: 15px;
}

/* END campaign style here */

